import React, { Component } from 'react'
import { Form, Input, Button, DatePicker, Switch, Spin, Select, notification } from 'antd';
import { addDev } from "../../api/request"
export default class Dev extends Component {
    constructor(){
        super()
        this.state={
            sping:false
        }
    }
    //
    openNotificationWithIcon = (type, msg) => {
        notification[type]({
            message: msg
        });
    };
    onfinish = (value) => {
        // 
        this.setState({
            sping:true
        })
        let flag = true
        let { devName, devNum, devDate, devSIM = "", devUse = false,devLocation = "", devAdmin = "" } = value;
        // console.log({ devName, devNum, devDate, devSIM, devUse, devLocation, devAdmin });
        // console.log(devDate._d);
        if (devLocation.split(",").length !== 2 && devLocation !== "") {
            flag = false
            // 
            this.setState({
                sping: false
            })
            this.openNotificationWithIcon('error', "")
        }
        if (flag) {
            //
            let query = { devName, devNum, devDate: devDate._d, devSIM, devUse, devLocation: devLocation.split(","), devAdmin }
            addDev(query).then(res => {
                console.log(res);
                if (res.data.state) {
                    //
                    this.openNotificationWithIcon('success', "")
                } else if(res.data.status===0&&res.data.msg===""){
                    //
                    this.openNotificationWithIcon('error', ",")
                }else{
                    this.openNotificationWithIcon('error', ",")
                }
            }).catch(err => {
                //
                this.openNotificationWithIcon('error', "")
            }).finally(()=>{
                // 
                this.setState({
                    sping: false
                })
            })
        }
    };
    render() {
        return (
            <div>
                <Spin spinning={this.state.sping}>
                    <Form
                        labelCol={{
                            span: 4,
                        }}
                        wrapperCol={{
                            span: 14,
                        }}
                        layout="horizontal"
                        onFinish={this.onfinish}
                        size="middle"
                    >
                        
                        <Form.Item label="" name="devName"
                            rules={[{ required: true, message: '' }]}>
                            <Select>
                                <Select.Option value="pH">pH</Select.Option>
                                <Select.Option value=""></Select.Option>
                                <Select.Option value=""></Select.Option>
                                <Select.Option value=""></Select.Option>
                                <Select.Option value=""></Select.Option>
                            </Select>
                        </Form.Item>
                        <Form.Item label="" name="devNum"
                            rules={[{ required: true, message: '!' }]}>
                            <Input />
                        </Form.Item>
                        <Form.Item label="" name="devDate"
                            rules={[{ required: true, message: '!' }]}>
                            <DatePicker placeholder="" />
                        </Form.Item>
                        <Form.Item label="SIM" name="devSIM">
                            <Input />
                        </Form.Item>
                        <Form.Item label="" valuePropName='checked' name="devUse">
                            <Switch />
                        </Form.Item>
                        <Form.Item label="" name="devLocation">
                            <Input placeholder="" />
                        </Form.Item>
                        <Form.Item label="" name="devAdmin">
                            <Input />
                        </Form.Item>
                        <Form.Item
                            wrapperCol={{
                                xs: { span: 24, offset: 0 },
                                sm: { span: 16, offset: 8 },
                            }}
                        >
                            <Button type="primary" htmlType="submit"></Button>
                        </Form.Item>
                    </Form>
                </Spin>
            </div>
        )
    }
}
